@import url(https://fonts.googleapis.com/css?family=Titillium+Web);
html,
body {
  font-family: Titillium\ Web, sans-serif;
  background-color: #000!important;
}

.show-cursor{
  cursor: pointer;
}

@media (max-width:767px){
  .featured-movie + .featured-movie{
    margin-top: 30px;
  }
}
.navbar[header-component].navbar-expand .container{
    padding-left: 15px;
    padding-right:15px;
}

.navbar[header-component] li a{
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
}

.navbar[header-component] li a:hover{
    -webkit-transform: scale(.9);
            transform: scale(.9);
}

.navbar[header-component] li a.active{
    -webkit-transform: scale(1);
            transform: scale(1);
}

@media (min-width : 768px){
    .navbar[header-component] li a{
        font-size: 24px;
    }
}
[movie] .btn-link{
    line-height: 1;
    padding: 0;
    display: inline-block;
    margin-top: -5px;
}
[star-rating] .stars-filled{
    top:0;
    left: 0;;
    overflow: hidden;
}
[user-rating].User-ratings{
    list-style-type: none;
    font-size: 14px;
}

[user-rating].User-ratings li + li{
    margin-top: 5px;
}
[image-loader].no-image{
    height: 300px;
}
[search] .plot-ddl{
    max-width: 120px;
}
[no-movie].no-content{
    min-height: 300px;
}

@media (max-width : 575px){
    [no-movie].no-content h2{
        font-size: 20px;
    }
}

.lds-ellipsis {
    display: inline-block;
    position: relative;
    width: 64px;
  }
  
  .lds-ellipsis div {
    display: inline-block;
    position: absolute;
    border-radius: 9999px;
    background-color: #fff;
    width: 11px;
    height: 11px;
    -webkit-animation-timing-function: cubic-bezier(0, 1, 1, 0);
            animation-timing-function: cubic-bezier(0, 1, 1, 0);
  }
  
  .lds-ellipsis div:nth-child(1) {
    left: 6px;
    -webkit-animation: lds-ellipsis1 .6s infinite;
            animation: lds-ellipsis1 .6s infinite;
  }
  
  .lds-ellipsis div:nth-child(2) {
    left: 6px;
    -webkit-animation: lds-ellipsis2 .6s infinite;
            animation: lds-ellipsis2 .6s infinite;
  }
  
  .lds-ellipsis div:nth-child(3) {
    left: 26px;
    -webkit-animation: lds-ellipsis2 .6s infinite;
            animation: lds-ellipsis2 .6s infinite;
  }
  
  .lds-ellipsis div:nth-child(4) {
    left: 45px;
    -webkit-animation: lds-ellipsis3 .6s infinite;
            animation: lds-ellipsis3 .6s infinite;
  }
  
  @-webkit-keyframes lds-ellipsis1 {
    0% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
  
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  
  @keyframes lds-ellipsis1 {
    0% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
  
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  
  @-webkit-keyframes lds-ellipsis3 {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  
    100% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
  }
  
  @keyframes lds-ellipsis3 {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  
    100% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
  }
  
  @-webkit-keyframes lds-ellipsis2 {
    0% {
      -webkit-transform: translate(0, 0);
              transform: translate(0, 0);
    }
  
    100% {
      -webkit-transform: translate(19px, 0);
              transform: translate(19px, 0);
    }
  }
  
  @keyframes lds-ellipsis2 {
    0% {
      -webkit-transform: translate(0, 0);
              transform: translate(0, 0);
    }
  
    100% {
      -webkit-transform: translate(19px, 0);
              transform: translate(19px, 0);
    }
  }
[movie-preview] .star-position{
    -webkit-transform: rotate(8deg);
            transform: rotate(8deg);
    top:0;
    left:5px;
}

@media (min-width:768px){
    [movie-preview] .star-position{
        top: -20px;
        left: -3px;
    }
}

